import React, { Component } from 'react';

import ResultComponent from './component/ResultComponent';
import KeyPadComponent from "./component/KeyPadComponent";
class App extends Component {
  constructor() {
    super();

    this.state = {
      result: "0",
      currentVaue: null
    }
  }
  onClick = button => {

    if (button === '+' || button === '-' || button === '*' || button === '/' || button === '%' || button === ".") {
      if (this.state.currentVaue == null) {
        this.setState({ currentVaue: button })
      } else if (this.state.currentVaue !== button) {
        this.setState({ currentVaue: button })
      } else {
        return;
      }
    }
    else {
      this.setState({ currentVaue: null })
    }

    if (button === "=") {
      this.calculate()
    }

    else if (button === "C") {
      this.reset()
    }
    else if (button === "CE") {
      this.backspace()
    }

    else if (this.state.result.toString().match(/^([0-9])[+\-*/0-9_.]*$/)) {
      const { result } = this.state
      this.setState({
        result: result === '0' ? String(button) : this.state.result + button
      })

    } else {
      const { result } = this.state
      this.setState({
        result: result === '0' ? String(button) : this.state.result + button
      })
    }
  }




  calculate = () => {
    var checkResult = ''
    if (this.state.result.includes('--')) {
      checkResult = this.state.result.replace('--', '+')
    } else {
      checkResult = this.state.result
    }

    try {
      this.setState({
        // eslint-disable-next-line
        result: eval(checkResult)
      })

    } catch (e) {
      this.setState({
        result: "error"
      })

    }
  };

  reset = () => {
    this.setState({
      result: "0"
    })
  };

  backspace = () => {
    if (this.state.result.length === 1) {
      this.setState({
        result: "0"
      })

    } else {
      this.setState({
        result: this.state.result.toString().slice(0, -1)
      })
      console.log("data", this.state.result.length)
    }
  };

  render() {
    return (
      <div>
        <div className="calculator-body">
          <div className="container">
            <div className="row jutify-content-center align-items-center" style={{ height: "80vh" }}>
              <div className="col-md-4 mx-auto">
                <div className="card m-auto" style={{ borderRadius: "30px" }} >
                  <div className="card-body" style={{ backgroundColor: "black", borderRadius: "30px" }}>
                    <div className="mb-3 ">
                      <ResultComponent result={this.state.result} />
                    </div>
                    <div className="container">
                      <KeyPadComponent onClick={this.onClick} />
                    </div>
                  </div>
                </div>
              </div></div>
          </div>
        </div>
      </div>
    );
  }
}

export default App;





