import React, { Component } from 'react';

import "./App.css"

class KeyPadComponent extends Component {

    render() {

        return (
            <div className="button">

                <div className="mb-2">
                    <button className="mr-2 mt-2 btn-secondary" name="C" onClick={e => this.props.onClick(e.target.name)}>C</button>
                    <button className="mr-2 btn-secondary" name="CE" onClick={e => this.props.onClick(e.target.name)}>CE</button>
                    <button className="mr-2 btn-secondary" name="%" onClick={e => this.props.onClick(e.target.name)}>%</button>
                    <button className="mr-2 btn-warning" name="/" onClick={e => this.props.onClick(e.target.name)}>รท</button>
                </div>

                <div className="mb-2">
                    <button className="mr-2" name="1" onClick={e => this.props.onClick(e.target.name)}>1</button>
                    <button className="mr-2" name="2" onClick={e => this.props.onClick(e.target.name)}>2</button>
                    <button className="mr-2" name="3" onClick={e => this.props.onClick(e.target.name)}>3</button>
                    <button className="mr-2 btn-warning" name="+" onClick={e => this.props.onClick(e.target.name)}>+</button>
                </div>

                <div className="mb-2">
                    <button className="mr-2" name="4" onClick={e => this.props.onClick(e.target.name)}>4</button>
                    <button className="mr-2" name="5" onClick={e => this.props.onClick(e.target.name)}>5</button>
                    <button className="mr-2" name="6" onClick={e => this.props.onClick(e.target.name)}>6</button>
                    <button className="mr-2 btn-warning" name="-" onClick={e => this.props.onClick(e.target.name)}>-</button>
                </div>

                <div className="mb-2">
                    <button className="mr-2" name="7" onClick={e => this.props.onClick(e.target.name)}>7</button>
                    <button className="mr-2" name="8" onClick={e => this.props.onClick(e.target.name)}>8</button>
                    <button className="mr-2" name="9" onClick={e => this.props.onClick(e.target.name)}>9</button>
                    <button className="mr-2 btn-warning" name="*" onClick={e => this.props.onClick(e.target.name)}>x</button>
                </div>

                <div className="mb-2">
                    <button className="mr-2" name="." onClick={e => this.props.onClick(e.target.name)}>.</button>
                    <button className="mr-2" style={{ width: "130px", borderRadius: "30px" }} name="0" onClick={e => this.props.onClick(e.target.name)}>0</button>
                    <button className="mr-2 btn-warning" name="=" onClick={e => this.props.onClick(e.target.name)}>=</button>

                </div>
            </div>

        );
    }
}


export default KeyPadComponent;